import React, { useState, useEffect, useMemo, useCallback } from 'react';
import axios from 'src/utils/axios';
import { AreaClosed, Line, Bar } from '@visx/shape';
import { AppleStock } from '@visx/mock-data/lib/mocks/appleStock';
import { curveMonotoneX } from '@visx/curve';
import { GridRows, GridColumns } from '@visx/grid';
import { scaleTime, scaleLinear } from '@visx/scale';
import {
  withTooltip,
  Tooltip,
  TooltipWithBounds,
  defaultStyles
} from '@visx/tooltip';
import { WithTooltipProvidedProps } from '@visx/tooltip/lib/enhancers/withTooltip';
import { localPoint } from '@visx/event';
import { LinearGradient } from '@visx/gradient';
import { max, extent, bisector } from 'd3-array';
import { timeFormat } from 'd3-time-format';
import { useTheme, useMediaQuery } from '@material-ui/core';
import { Chart } from 'src/types/stock';

type TooltipData = AppleStock;

const background = '#3b6978';
const background2 = '#204051';
const accentColor = '#edffea';
const accentColorDark = '#75daad';
const tooltipStyles = {
  ...defaultStyles,
  background,
  border: '1px solid white',
  color: 'white'
};
const formatDate = timeFormat("%b %d, '%y");
const getDate = (d: AppleStock) => new Date(d.date);
const getStockValue = (d: AppleStock) => d.close;
const bisectDate = bisector<AppleStock, Date>(d => new Date(d.date)).left;

interface VisxWrapperProps {
  className?: string;
  path: string;
  period: string;
  width?: number
}

export default withTooltip<VisxWrapperProps, TooltipData>(
  ({
    path,
    period,
    width,
    showTooltip,
    hideTooltip,
    tooltipData,
    tooltipTop = 0,
    tooltipLeft = 0
  }: VisxWrapperProps & WithTooltipProvidedProps<TooltipData>) => {
    const theme = useTheme();
    const [results, setResults] = useState<any[]>([]);
    const mobileDevice = useMediaQuery(theme.breakpoints.down('sm'));
    const innerWidth = mobileDevice ? 150 : width;
    const innerHeight = (innerWidth * 9) / 16;

    const getData = async (value: string, type: string) => {
      const response = await axios.get<Chart[]>(`/stock/chart/${value}`, {
        params: { type }
      });
      setResults(response.data);
    };

    useEffect(() => {
      path && getData(path, period);
    }, [path, period]);

    const dateScale = useMemo(
      () =>
        scaleTime({
          range: [0, innerWidth],
          domain: extent(results, getDate) as [Date, Date]
        }),
      [innerWidth, results]
    );

    const stockValueScale = useMemo(
      () =>
        scaleLinear({
          range: [innerHeight, 0],
          domain: [0, (max(results, getStockValue) || 0) + innerHeight / 3],
          nice: true
        }),
      [innerHeight, results]
    );

    const handleTooltip = useCallback(
      (
        event:
          | React.TouchEvent<SVGRectElement>
          | React.MouseEvent<SVGRectElement>
      ) => {
        const { x } = localPoint(event) || { x: 0 };
        const x0 = dateScale.invert(x);
        const index = bisectDate(results, x0, 1);
        const d0 = results[index - 1];
        const d1 = results[index];
        let d = d0;
        if (d1 && getDate(d1)) {
          d =
            x0.valueOf() - getDate(d0).valueOf() >
            getDate(d1).valueOf() - x0.valueOf()
              ? d1
              : d0;
        }
        showTooltip({
          tooltipData: d,
          tooltipLeft: x,
          tooltipTop: stockValueScale(getStockValue(d))
        });
      },
      [showTooltip, stockValueScale, dateScale, results]
    );

    return (
      <div>
        {results.length > 0 && (
          <>
            <svg width={innerWidth} height={innerHeight}>
              <rect
                x={0}
                y={0}
                width={innerWidth}
                height={innerHeight}
                fill="url(#area-background-gradient)"
                rx={14}
              />
              <LinearGradient
                id="area-background-gradient"
                from={background}
                to={background2}
              />
              <LinearGradient
                id="area-gradient"
                from={accentColor}
                to={accentColor}
                toOpacity={0.1}
              />
              <GridRows
                left={0}
                scale={stockValueScale}
                width={innerWidth}
                strokeDasharray="1,3"
                stroke={accentColor}
                strokeOpacity={0}
                pointerEvents="none"
              />
              <GridColumns
                top={0}
                scale={dateScale}
                height={innerHeight}
                strokeDasharray="1,3"
                stroke={accentColor}
                strokeOpacity={0.2}
                pointerEvents="none"
              />
              <AreaClosed<AppleStock>
                data={results}
                x={d => dateScale(getDate(d)) ?? 0}
                y={d => stockValueScale(getStockValue(d)) ?? 0}
                yScale={stockValueScale}
                strokeWidth={1}
                stroke="url(#area-gradient)"
                fill="url(#area-gradient)"
                curve={curveMonotoneX}
              />
              <Bar
                x={0}
                y={0}
                width={innerWidth}
                height={innerHeight}
                fill="transparent"
                rx={14}
                onTouchStart={handleTooltip}
                onTouchMove={handleTooltip}
                onMouseMove={handleTooltip}
                onMouseLeave={() => hideTooltip()}
              />
              {tooltipData && (
                <g>
                  <Line
                    from={{ x: tooltipLeft, y: 0 }}
                    to={{ x: tooltipLeft, y: innerHeight + 0 }}
                    stroke={accentColorDark}
                    strokeWidth={2}
                    pointerEvents="none"
                    strokeDasharray="5,2"
                  />
                  <circle
                    cx={tooltipLeft}
                    cy={tooltipTop + 1}
                    r={4}
                    fill="black"
                    fillOpacity={0.1}
                    stroke="black"
                    strokeOpacity={0.1}
                    strokeWidth={2}
                    pointerEvents="none"
                  />
                  <circle
                    cx={tooltipLeft}
                    cy={tooltipTop}
                    r={4}
                    fill={accentColorDark}
                    stroke="white"
                    strokeWidth={2}
                    pointerEvents="none"
                  />
                </g>
              )}
            </svg>
            {tooltipData && (
              <div>
                <TooltipWithBounds
                  key={Math.random()}
                  top={tooltipTop - 12}
                  left={tooltipLeft + 12}
                  style={tooltipStyles}
                >
                  {`$${getStockValue(tooltipData)}`}
                </TooltipWithBounds>
                <Tooltip
                  top={innerHeight - 14}
                  left={tooltipLeft}
                  style={{
                    ...defaultStyles,
                    minWidth: 72,
                    textAlign: 'center',
                    transform: 'translateX(-50%)'
                  }}
                >
                  {formatDate(getDate(tooltipData))}
                </Tooltip>
              </div>
            )}
          </>
        )}
      </div>
    );
  }
);
